
import { defineComponent } from 'vue'
import pt, { Element } from 'periodic-table'

export default defineComponent({
  name: 'Element',
  props: {
    userEditable: Boolean,
    symbol: {
      type: String,
      required: true
    },
    valenceElectrons: {
      type: Number,
      required: true
    },
    electronAngle: {
      type: Number,
      required: true
    },
    size: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      elementHeight: 0
    }
  },
  computed: {
    element (): Element {
      return pt.symbols[this.symbol]
    },
    fontSize (): string {
      return (this.size * 110 / 300).toString()
    }
  },
  methods: {}
})
